}
@font-face {
	font-family: 'Proxima Nova';
	src: url('../fonts/ProximaNova/ProximaNova-Black.ttf') format('truetype');
	src: url('../fonts/ProximaNova/ProximaNova-Black.eot') format('embedded-opentype');
	src: url('../fonts/ProximaNova/ProximaNova-Black.woff') format('woff');
