}
@font-face {
	font-family: 'Proxima Nova';
	src: url('../fonts/ProximaNova/ProximaNova-Bold.ttf') format('truetype');
	src: url('../fonts/ProximaNova/ProximaNova-Bold.eot') format('embedded-opentype');
	src: url('../fonts/ProximaNova/ProximaNova-Bold.woff') format('woff');
	font-weight: bold;
}

body {
	font-family: $font-proxima;
	background-color: #f9fcff;
	overflow-x: hidden;
	* {
		&:before, &:after {
			transition: .25s;
		}
		transition: .25s;
	}
}
a {
	text-decoration: none;
	outline: none;
	&:hover{
		text-decoration: none;
	}
}

// ******menu button  start** 
.main__btn__menu {
	max-width: 50px;
	height: 50px;
	border: 1px solid $accent-color;
	border-radius: 3px;
	padding: 9px;
	cursor: pointer;
	position: absolute;
	right: 15px;
	top: 15px;
	transition: .4em;
	box-shadow: 0 0 5px 1px rgba($accent-color, 0.8);
	span {
		background: #fff;
		height: 3px;
		width: 70%;
	}
	&.open {
		span:nth-child(1) {
			transform: translateY(10px) rotate(135deg);
		}
		span:nth-child(2) {
			transform: scale(0);
		}
		span:nth-child(3) {
			transform: translateY(-10px) rotate(-135deg);
		}
	}
}


// ***** menu button end
.icon-heart:before {
	transform: rotateY(180deg);
}
.watch__btn {
	border-color: #fff;
	position: relative;
	z-index: 10;
	height: 33px;
	border: 1px solid #fff;
	border-radius: 3px;
	padding-left: 20px;
	padding-right: 20px;
	display: inline-block;
	text-align: center;
	color: #fff;
	font-size: 13px;
	line-height: 30px;
	&:hover {
		background: #fff;
		color: rgba(243, 126, 82, 0.7);
	}
}
.img {
	border: 10px solid #000;
	height: 185px;
	position: relative;
	&:before {
		position: absolute;
		left: 10px;
		display: block;
		width: 37px;
		height: 23px;
		font-weight: bold;
		font-size: 13px;
		color: #fff;
		text-align: center;
		clip-path: polygon(0 0, 100% 0, 100% 100%, 50% 20px, 0 100%);
	}
	&.hit {
		&:before {
			background-color: #f07f54;
			content: '';
		}
	}
	&.new {
		&:before {
			content: 'NEW';
			background-color: #764921;
		}
	}
}
// *******HEADER START*******
header {
	min-height: 100px;
	background: $black-bg;
	padding-bottom: 30px;
	padding-top: 30px;
	.desc {
		font-size: 14px;
		color: $white;
		margin: 0;
		max-width: 195px;
		font-weight: 300;
		line-height: 16px;
		letter-spacing: -0.5px;
	}
	.input__search {
		position: relative;
		.icon-search {
			position: absolute;
			left: 12px;
			top: 11px;
			&:before {
				transform: rotateY(180deg);
				color: $accent-color;
				font-size: 20px;
			}
		}
		input {
			min-width: 380px;
			border: 1px solid $grey-border;
			background-color: transparent;
			padding: 9px;
			padding-left: 55px;
			border-radius: 4px;
			color: $white;
			transition: .25s;
			&::placeholder {
				font-style: italic;
				font-size: 13px;
				line-height: 21px;
				font-family: $font-proxima;
			}
			&:focus {
				border-color: $accent-color;
			}
		}
	}
	a {
		color: $white;
		text-align: center;
		padding-left: 20px;
		padding-right: 20px;
		&[class^="icon-"]:before, &[class*="icon-"]:before {
			font-size: 20px;
			color: $color-icon;
		}
		&.user__btn {
			font-size: 14px;
			height: 36px;
			display: inline-block;
			line-height: 18px;
			&:hover {
				color: $accent-color;
			}
			&::before {
				color: #ba7862;
				transform: rotateZ(-135deg);
				margin-right: 12px;
				padding: 0;
				font-size: 14px;
			}
		}
		&.favorite__btn, &.cart__btn {
			position: relative;
			border-left: 1px solid #595a5b;
			height: 36px;
			display: inline-block;
			line-height: 38px;
			span {
				position: absolute;
				display: inline-block;
				width: 15px;
				height: 15px;
				border-radius: 50%;
				background: #f37e52;
				color: #fff;
				font-size: 11px;
				line-height: 15px;
				text-align: center;
				right: 17px;
				top: 2px;
			}
			&:hover {
				background: rgba($white, .75);
			}
		}
	}
}

// *********HEADER END******

// *********NAV START***********
nav {
	background: $black-bg;
}
.nav {
	.nav-item {
		border-left: 1px solid #2a1f20;
		&:first-child {
			border: none;
		}
		&:hover {
			background: rgba(212, 49, 64, 0.16);
		}
		.dropdown-menu {
			border-radius: 0;
			padding: 0;
			opacity: 0.95;
			background-color: #dbdada;
			box-shadow: 0px 3px 40px 0px rgba(0, 0, 0, 0.47);
			top: -3px !important;
			left: -2px !important;
			.dropdown-item {
				font-size: 15px;
				line-height: 55px;
				position: relative;
				padding-left: 10px;
				&:after {
					content: '';
					position: absolute;
					display: block;
					bottom: 0;
					width: 80%;
					left: 10%;
					background-color: rgba(248, 127, 82, 0.22);
					height: 1px;
				}
				&:last-child {
					&:after {
						display: none;
					}
				}
			}
		}
		.dropdown-toggle {
			position: relative;
			&:after {
				content: '\e803';
				font-family: fontello;
				border: none;
				color: #f37e52;
				font-size: 18px;
				position: absolute;
				right: 20px;
				bottom: 45px;
			}
		}
	}
	.nav-link {
		color: $white;
		font-size: 15px;
		text-transform: uppercase;
		padding: 19px 37px;
		&.capro {
			text-transform: none;	
		}
	}
}
.nav .nav-item:first-child .dropdown-toggle:after {
	visibility: hidden;
}

// ***********NAV END**********

// *******MAIN PAGE START************

.slider {
	height: 390px;
}
.slider__item {
	background: url('../img/header-bg.jpg');
	background-size: cover;	
	color: $white;
	padding-left: 85px;
