		z-index: 20;
	}
	.bottom__desc {
		font-size: 15px;
		position: relative;
		z-index: 20;
	}
	h1 {
		font-weight: 900;
		font-size: 30px;
		text-transform: uppercase;
		margin-top: 40px;
		margin-bottom: 0;
