			color: #fff;
			background-color: rgba(39, 25, 12, 0.7);
			text-align: center;
			position: absolute;
			z-index: 20;
			top: 0;
			bottom: 0;
			right: 0;
			left: 0;
			color: #fff;
			.heading {
				font-size: 26px;
				line-height: 28px;
				font-weight: 300;
				text-transform: uppercase;
				strong {
					display: block;
					font-weight: bold;
					color: #f07f54;
				}
			}
			p {
				font-size: 14px;
				padding: 15px 150px;
				margin: 0;
			}
		}
		&:hover {
			.desc {
				background-color: rgba(200, 78, 32, .7);
				strong {
					color: #fff;
				}
			}
