// *******POLIGRAFIA START********
.typography {
	&__slider {
		min-height: 280px;
		.slider__item {
			background: url('../img/typography.jpg') center no-repeat;
			background-size: cover;
			&::before {
				content: '';
				background-color: rgba(29, 28, 27, 0.8);
				position: absolute;
				display: block;
				top: 0;
				bottom: 0;
				right: 0;
				left: 0;
			}
		}
		.slick-slide {
			transform: scale(.85);
			&.slick-center {
				transform: scale(1);
				z-index: 20;
			}
		}
		.slick-prev {
			left: -60px;
		}
		.slick-next {
			right: -60px;
		}
		.slick-dots {
			top: 20px;
			right: 25%;
			width: auto;

			li {
				margin: 0;
			}
		}
	}
}
.poligrafia {
	padding-top: 30px;
	h1 {
		font-size: 22px;
		line-height: 18px;
		font-weight: bold;
		color: #202020;
		text-transform: uppercase;
		margin-bottom: 15px;
	}
	p {
		font-size: 13px;
		line-height: 15px;
		color: #3e3e3e;
	}
}
.our__projects {
	span {
		display: block;
		background-color: #f87f52;
		font-weight: bold;
		font-size: 18px;
		line-height: 18px;
		color: #fcfcfc;
		padding-left: 20px;
		padding-top: 15px;
		padding-bottom: 15px;
		margin-bottom: 35px;
		margin-top: 35px;
		position: relative;
		cursor: pointer;
		&:hover {
			background: darken(#f87f52, 15%);
			&:after {
				color: darken(#f87f52, 15%);
			}
		}
		&:after {
			content: '+';
			display: block;
			width: 18px;
			font-weight: 900;
			height: 18px;
			color: #f87f52;
			background-color: #fcfcfc;
			text-align: center;
			font-size: 20px;
			line-height: 20px;
			padding-left: 1px;
			position: absolute;
			right: 26px;
			bottom: 15px;
			border-radius: 2px;
		}
		&.open {
			&:after {
				content: '';
				line-height: 17px;
			}
		}
	}
	p {
		font-size: 13px;
		line-height: 15px;
		color: #3e3e3e;
		margin: 0;	
		display: none;
		margin-bottom: 20px;
	}
}

