// ******CATALOG START*******

.catalog {
	padding-top: 20px;
	.popular {
		margin-top: 0;
		&__slider__item {
			padding: 30px 15px;
			max-width: none;
		}
		&:before {
			display: none;
		}
	}
}
.filter {
	ul {
		list-style: none;
	}
	.menu {
		padding: 0;
		border: 1px solid #e6e6e6;
		border-radius: 3px;
		&-link {
			color: #050505;
			font-size: 15px;
			line-height: 22px;
			position: relative;
		}
		&-item {
			padding: 18px 15px;
			position: relative;
			cursor: pointer;
			z-index: 30;
			&:hover {
				background: #ececec;
			}
			&:last-child {
				&:after {
					display: none;
				}
			}
			.toggle {
				width: 18px;
				height: 18px;
				display: inline-block;
				border-radius: 3px;
				font-size: 18px;
				position: absolute;
				right: 20px;
				text-align: center;
				cursor: pointer;
				color: #fff;
				line-height: 18px;
				background: #f87f52;
				&:hover {
					background: darken(#f87f52, 15%);
				}
			}
			&.active {
				.toggle {
					border: 2px solid #f87f52;
					line-height: 12px;
					color: #f87f52;
					background: #fff;
					font-size: 24px;
				}
			}
			&:after {
				content: '';
				display: block;
				height: 1px;
				width: 90%;
				background: #e6e6e6;
				position: absolute;
				bottom: 0;
			}
		}
		.submenu-link {
			color: #050505;
			font-size: 13px;
			line-height: 37px;
			position: relative;
			&:hover {
				color: #f87f52;
				text-decoration: underline;
			}
			&:before {
				content: '';
				position: absolute;
				font-size: 13px;
				left: -19px;
				top: -11px;
				color: #f87f52;
			}
		}
		.submenu {
			padding-left: 40px;
			display: none;
		}
	}
	&__price {
		font-size: 15px;
		color: #050505;
		padding: 15px;
		padding-bottom: 5px;
		border: 1px solid #e6e6e6;
		border-radius: 3px;
		.price {
			display: block;
			margin-bottom: 20px;
			&__from, &__to {
				border: 1px solid #e6e6e6;
				border-radius: 3px;
				width: 48%;
				display: inline-block;
				text-align: center;
				padding: 10px;
			}
			&__from {
				margin-right: 2%;
			}
			
		}
		ul {
			margin-top: 25px;
			padding: 0;
			.filter__price--item {
				font-size: 15px;
				border: 1px solid #e6e6e6;
				border-radius: 3px;
				margin-bottom: 10px;
				position: relative;
				z-index: 30;
				a {
					color: #050505;
					display: block;
					padding: 15px;
					cursor: pointer;
					&:hover {
						background: #ececec;
					}
					[class^="icon-"], [class*="icon-"] {
						position: absolute;
						right: 10px;
						border: 2px solid #f87f52;
						display: inline-block;
						width: 18px;
						height: 18px;
						border-radius: 3px;
						text-align: center;
						&:hover {
							background: #fff;
						}
						&:before {
							color: #f87f52;
							position: absolute;
							right: -4px;
						}
					}
				}
				.submenu {
					display: none;
				}
			}
		}
		.btn__result {
			font-size: 15px;
			display: inline-block;	
			padding: 15px;
			border: 1px solid #f87f52;
			border-radius: 3px;
			margin-bottom: 10px;
			color: #050505;
			text-transform: uppercase;
			text-align: center;
			&:hover {
				background-color: #f87f52;
				color: #fff;
			}
		}
	}
}
.pagination {
	font-size: 13px;
	margin-bottom: 30px;
	li a {
		display: inline-block;
		margin-left: 10px;
		text-align: center;
		line-height: 20px;
		color: #050505;
		position: relative;
		&.active {
			background: #f87f52;
			width: 22px;
			height: 22px;
			color: #fff;
			border-radius: 8px;
			margin-right: 5px;
			margin-left: 15px;
		}
		&:first-child {
			&:before {
				content: '<';
				color: #f87f52;
				margin-right: 15px;				
			}
		}
		&:last-child {
			&:after {
				content: '>';
				color: #f87f52;
				margin-left: 15px;
			}
		}
	}
}
// ******CATALOG   END*******

