					width: 30px;
					height: 25px;
					text-align: center;
					border-radius: 3px;
					line-height: 30px;
					margin-right: 8px;
					margin-left: 8px;
				}
			}
			del {
				color: #949494;
				font-size: 16px;
				display: block;
				margin-bottom: 20px;
				margin-top: 25px;
			}
			.new__price {
				font-weight: bold;
				font-size: 32px;
				color: #050505;
				display: block;
				margin-bottom: 20px;
			}
			.btn__buy {
				display: inline-block;
				color: #502a12;
				text-transform: uppercase;
				font-size: 14px;
				line-height: 16px;
				padding: 20px 30px;
				border-radius: 4px;
				border: 1px solid #f87f52;
				&:hover {
					background: #f87f52;
					color: #fff;
				}
			}
			.to__cart {
				display: inline-block;
				color: #fff;
				text-transform: uppercase;
				background: #f87f52;
				padding: 20px;
				margin-left: 15px;
				border-radius: 4px;
