/*media*/
@include media-breakpoint-down(lg) {
	header {
		.input__search input {
			min-width: auto;
		}
		a.user__btn {
			padding-left: 0;
		}
		.logo {
			margin-bottom: 20px;
		}
	}
	nav {
		.nav {
			.nav-link {
				padding: 19px 33px;
			}
		}
	}
	.main_article .video__block .btn__play {
		margin-top: -40px;
	}
	.typography__slider .slick-slide {
		transform: scale(1);
	}
	.typography__slider .slick-dots {
		right: 20px;
	}
	.typography__slider .slick-prev {
		left: 30px;
	}
	.typography__slider .slick-next {
		right: 30px;
	}
}


@include media-breakpoint-down(md) {
	nav {
		.nav {
			.nav-link {
				padding: 19px 24px;
			}
		}
	}
	.get__images, .new__offer {
		height: 200px;
	}
	.gallery__item .desc p {
		padding: 15px;
	}
	footer {
		.desc {
			margin-bottom: 15px;
			margin-top: 15px;
		}
		.subscribe strong {
			margin-top: 15px;
		}
	}
	.main_article .video__block .btn__play {
		margin-top: 0;
	}
}

@include media-breakpoint-down(sm) {
	header {
		padding-left: 30px;
		padding-top: 10px;
		padding-bottom: 10px;
		a {
			padding-left: 10px;
			padding-right: 10px;
		}
		.input__search {
			margin-bottom: 20px;
			margin-top: 20px;
		}
		.logo {
			margin-bottom: 0;
		}
	}
	nav {
		.nav {
			width: 100%;
			.nav-item {
				border: none;
			}
		}
		&.main__menu {
			display: none;
			max-width: 250px;
			margin: 0 auto;
			border: 1px solid #ececec;
			border-radius: 0px;	
			position: absolute;
			top: 76px;
			right: 15px;
			z-index: 150;
			box-shadow: 0 0 15px 2px rgba(0, 0, 0, 0.4)
		}
	}
	.popular .nav .nav-item .nav-link {
		padding: 0 20px;
	}
	.features__circle {
		background: none;
		[class^="item"], [class*="item"] {
			position: relative;
			top: auto;
			right: auto;
			left: auto;
			bottom: auto;
			margin: 0;
			text-align: left;
			max-width: none;
			padding: 10px 30px;
			font-size: 16px;
			.number {
				font-weight: 900;
				font-size: 40px;
				color: #f87f52;
				display: inline-block;
				padding-right: 15px;
			}
		}
		.item9 {
			font-weight: bold;
			font-size: 20px;
			text-transform: uppercase;
		}
	}	
	.catalog__item__card .description {
		text-align: center;
		margin-top: 20px;
		h1 {
			margin-top: 10px;
			margin-bottom: 10px;
		}
		.icon-heart {
			margin-top: 10px;
			margin-bottom: 10px;
		}
		form {
			margin-top: 15px;
			margin-bottom: 10px;
			&:before, &:after {
				width: 100%;
			}
		}
		del {
			margin-bottom: 10px;
		}
		.new__price {
			margin-bottom: 10px;
		}
		p  {
			padding-left: 0;
		}
	}
	.review__item {
		padding-right: 0;
		&--photo {
			margin-bottom: 15px;
			margin-left: 0;
		}
	}
	.review__item .add__comment, .review__item .hide__comment {
		margin: 10px auto;
	}

}
@include media-breakpoint-down (xs) {
	.slick-prev {
		left: 0!important;
	}
	.slick-next {
		right: 0!important;
	}
	.slider__item {
		padding-left: 35px;
		h1 {
			font-size: 20px;
		}
		.desc__heading {
			font-size: 16px;
		}
	}
	.popular .nav .nav-item {
		border-left: 1px solid #2a1f20;
	}
	footer .subscribe__form input {
		width: 150px;
	}
	// nav .nav .nav-link {
	// 	padding: 0;
	// }
	.main_article p, .certificate p {
		font-size: 14px;
		line-height: 18px;
	}
	.catalog__item__card .description .btn__buy, .catalog__item__card .description .to__cart {
		padding: 10px 15px;
		font-size: 10px;
	}
.add__comment__form .add__comment__btn {
		font-size: 12px;
		padding: 12px;
	}
	.review__item {
		&--desc {
			padding-left: 25px;
		}
		&--name {
			font-size: 14px;
		}
	}
	.our__projects span::after {
		right: 15px;
	}
}